import React from 'react';
import './css/Introduction.css'

function Intro(props) {
    return (
     
       <div className="Intro">
           <center><img className="profilepic" src={props.content.image}/></center>
            <h1>{props.content.name}</h1>
           <div className="ull"> <ul>
                <li>Web Developer</li>
                <li>IOT Developer</li>
            </ul>
            </div>
            <p>{props.content.text}</p>
    </div>
    
    
)
}
export default Intro;